
import swal from 'sweetalert';


export const cleanUpData = (abortController) =>
{
    abortController.abort();
}

export const instantiateAbort = () =>
{
    const abortController = new AbortController();
    const signal = abortController.signal;
    return abortController;
}

export const redirectIfTokenExist = (tokenIdentifier,props) =>
{
    let token = localStorage.getItem(tokenIdentifier);
    if(token !="" && token != null)
    {
      props.history.push("/user");
    }
}


export const redirectIfTokenExpired = (tokenIdentifier,history) =>
{
 localStorage.removeItem(tokenIdentifier);
  history.push("/login");
}


