import React,{useState,useEffect} from 'react'
import CIcon from '@coreui/icons-react'
import {
  CButton,
  CCol,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow,
  CAlert,
 
} from '@coreui/react'
import { Formik } from 'formik';
import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'
 import {
     cleanUpData,
     instantiateAbort
    } from '../../../helpers/componentHelperFunc'

    import {
        useDispatch,
         useSelector
        } from 'react-redux';
        
    import '../styles.css'
import { LoginAction, 
    clearLoginAuthState } from '../../../store/Actions/AuthActions';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner'
import { useHistory } from 'react-router-dom';

export default function LoginForm() {

   
    const [fields,setValues,handleFieldChange] = useFormFields({
        email: "",
        password: "",
      });  

      const [disable, setDisable] = useState(true);

      const dispatch =  useDispatch();
    
      const authResponse = useSelector(state => state.auth.loginResponse);

      const abortEffect = instantiateAbort();

      const history = useHistory();

const validation = () =>
{
 const errors = {};
  
errors.email =  !fields.email? 'Email Field is Required': '';
errors.email = !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(fields.email)?'Invalid email address':'';
errors.password =  !fields.password? 'Password Field is Required': '';
    
 return errors;
}

 const goToForgotPasswordPage = ()=>
 {
     history.push('/forgot-password');
 }

 /**
   * ensure button remains disable until all form fields are filled
   */
  useEffect(() => 
  {
    disableButtonIfFieldsAreEmpty(fields);

    return function cleanup()
      {
        cleanUpData(abortEffect);
      }

   }, [fields]); 

const UserLogin = (e) =>
{
  e.preventDefault();
  dispatch(LoginAction(fields,history));
}  


const disableButtonIfFieldsAreEmpty = (dataObject)=>
{
 disableSubmitButton(dataObject,setDisable);
}


const successMessage = (message) =>
{
    console.log(message);
    return;
  
}

 
const displayErrorMessages = (error,message)=>
{
    
    if(typeof error === "undefined")
    {
        return(
            <CAlert
            color="danger"
            closeButton
            onClick={closeBtnAction}
          >
           {message}
          </CAlert>
        );
    }
    
   return  Object.keys(error).map((keys,index)=>{
      
        return(
            <CAlert
            color="danger"
            closeButton
            onClick={closeBtnAction}
            key={keys}
          >
          {error[keys][0]}
          </CAlert>
        );
     
      });

}

 


  const closeBtnAction = () =>
  {
      dispatch(clearLoginAuthState());
  }





    return (
        <Formik
      initialValues={fields}
      validate={validation}
    >
      {({
        errors,
        touched,
        handleBlur,
        /* and other goodies */
      }) => (

        <CForm  onSubmit={UserLogin}>

       
    {
      /**
       * if the value of the state is loading
       */
      authResponse!="" && authResponse == "loading"?
      <>
	<Loader type="ThreeDots" color="#321fdb" height={60} width={60} />
      </>
      : 
      /**
       * if authResponse.success is true show success message
       */
    authResponse!="" && authResponse.success==true? 
    successMessage(authResponse.message)
    /**
     * else if authResponse.success == false show error messages
     */
       : 
      authResponse.success==false ?
      displayErrorMessages(authResponse.error,authResponse.message) 
      : authResponse

    }

        <h1>Login </h1>
        <p className="text-muted">Sign In to your account</p>
        <CInputGroup className="mb-3">
          <CInputGroupPrepend>
            <CInputGroupText>
              <CIcon name="cil-user" />
            </CInputGroupText>
          </CInputGroupPrepend>
          <CInput type="text"
           id="email"
           name="email"
           value = {fields.email}
           onChange={handleFieldChange}
           onBlur={handleBlur}
          placeholder="Username" autoComplete="username" />
         
         <div className="error_form_response">
          {
            errors.email && touched.email && errors.email
          }
        </div>
       
        </CInputGroup>

       

        <CInputGroup className="mb-4">
          <CInputGroupPrepend>
            <CInputGroupText>
              <CIcon name="cil-lock-locked" />
            </CInputGroupText>
          </CInputGroupPrepend>
          <CInput type="password"
           id="password"
           value = {fields.password}
           onChange={handleFieldChange}
           onBlur={handleBlur}
          placeholder="Password" autoComplete="current-password" />

<div className="error_form_response">
          {
            errors.password && touched.password && errors.password
          }
        </div>

        </CInputGroup>
       
        

        <CRow>
          <CCol xs="6">
            <CButton color="primary" 
             disabled={disable}
            type="submit" className="px-4">Login</CButton>
          </CCol>
          <CCol xs="6" className="text-right">
            <CButton onClick={goToForgotPasswordPage} color="link" className="px-0">Forgot password?</CButton>
          </CCol>
        </CRow>

      </CForm>

)}

</Formik>

    )
}
