import { 
    RESTART_LOGIN_RESPONSE, 
    LOGIN_SUCCESS, 
    LOGIN_CODE_ERROR, 
    LOGIN_ERROR, 
    LOGIN_LOADING,
    RESTART_FORGOT_PASSWORD_RESPONSE,
    FORGOT_PASSWORD_LOADING,
    FORGOT_PASSWORD_ERROR,
    FORGOT_PASSWORD_SUCCESS,
    FORGOT_PASSWORD_CODE_ERROR,
    RESTART_RESET_PASSWORD_RESPONSE,
    RESET_PASSWORD_LOADING,
    RESET_PASSWORD_SUCCESS,
    RESET_PASSWORD_ERROR,
    RESET_PASSWORD_CODE_ERROR
    } from "../ActionTypes/AuthTypes";
import { LoginService, ForgotPasswordService, ResetPasswordService } from "../../services/AuthService";
import Cookies from 'js-cookie'

  

export const LoginAction = (credentials,browserHistory) =>
{
   return (dispatch)=>{
 
       dispatch({type:RESTART_LOGIN_RESPONSE});
       dispatch({type:LOGIN_LOADING});

    LoginService(credentials).then((res)=>{

               console.log(res);

               if(res.hasOwnProperty('success') && res.success==true){
                dispatch({type:LOGIN_SUCCESS,res});
                console.log(res.token);
                Cookies.set('vt-music',res.token);
                browserHistory.push('/');



               }else if(res.hasOwnProperty('success') && res.success==false) {
                    dispatch({type:LOGIN_ERROR,res});  
               }
           },
           error=>{
            dispatch({type:LOGIN_CODE_ERROR,error});
               console.log(error);
           }
       )
   } 

}


export const clearLoginAuthState = ()=>
{
   return (dispatch)=>
   {
       dispatch({type:RESTART_LOGIN_RESPONSE});
   }
}



